.breed-selection-en {
  transition: all .3s;
  width: 550px;
  height: 230px;
  margin: 0 auto;
  display: block;
  line-height: 400px; }

.form-text-en {
  vertical-align: middle;
  line-height: 1.5;
  margin: 0 auto;
  height: 10vh;
  margin-top: 20vh; }

.form-select-en {
  height: 150px;
  position: relative; }

#form-title-en {
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  font-size: 2.2em;
  margin-bottom: 0;
  letter-spacing: -1px; }

#form-subtitle-en {
  font-size: 1.1em;
  text-transform: uppercase;
  margin-top: 0;
  text-align: center; }

@media only screen and (min-width: 320px) and (orientation: landscape) {
  .form-text-en {
    margin-top: -15vh;
    max-width: 350px;
    position: relative;
    height: 30vh; }
  .breed-selection-en {
    width: 65vw;
    height: 98vh;
    padding: 5px; }
  .form-select-en {
    height: 30vh;
    bottom: 25vh;
    width: 65vw;
    position: absolute; } }

@media only screen and (min-width: 320px) and (orientation: portrait) {
  .breed-selection-en {
    width: 96vw;
    height: 98vh;
    padding: 5px; }
  .form-text-en {
    position: relative;
    height: 30vh; }
  .form-select-en {
    height: 30vh;
    bottom: 25vh;
    width: 94vw;
    position: absolute; } }

@media only screen and (min-width: 576px) and (orientation: landscape) {
  .form-text-en {
    margin-top: 0vh; }
  .form-select-em {
    width: 60vw;
    top: 0; }
  .form-text-en {
    margin-top: -10vh; } }

@media only screen and (min-width: 768px) and (orientation: portrait) {
  .breed-selection-en {
    width: 550px;
    height: 230px;
    padding: 10px; }
  .form-text-en {
    height: 10vh; }
  .pt-form-select-en {
    height: 150px;
    position: relative;
    bottom: 0; }
  .form-select-en {
    width: 70vw;
    position: relative;
    top: 0vh; }
  #select-button-img {
    width: 68vw; } }

@media only screen and (min-width: 768px) and (orientation: landscape) {
  .breed-selection-en {
    width: 54vw;
    height: 230px;
    padding: 10px; }
  .form-text-en {
    height: 10vh;
    margin-top: -10vh; }
  .pt-form-select-en {
    height: 150px;
    position: relative;
    bottom: 0; }
  .form-select-en {
    height: 150px;
    position: relative;
    width: 55vh; } }

@media only screen and (min-width: 1024px) and (orientation: landscape) {
  .form-text-en {
    height: 10vh;
    margin-top: 0vh; }
  .breed-selection-en {
    margin-top: 10vh; } }

@media only screen and (min-width: 1024px) and (orientation: portrait) {
  .breed-selection-en {
    width: 70vw;
    height: 230px; } }
